import React from 'react';
import './App.css';
import axios from 'axios';
import $ from "jquery"; 
import ParticlesBg from 'particles-bg'
import {BitWallet, BETS,INTENSITY_TYPE,WALLET_TYPE,CYCLE_TYPE} from './BETSDATA'

let globalbets:BitWallet[] =[]

function App() {

  function updateHtmlComponents(betList: BitWallet[]){
    for(let counter=0;counter<betList.length;++counter){
      let testUndefined =  betList[counter].blockdata
      if( testUndefined !== undefined){

      let balance = testUndefined.balance * 0.8
      let sent = testUndefined.sent
      let recieved = testUndefined.received

      $("#balance"+betList[counter].code).text(balance + " BTC")
      $("#sent"+betList[counter].code).text(sent+ " BTC")
      $("#received"+betList[counter].code).text(recieved+ " BTC")
      }
    }
  }

  function betsRenderer(bets:BitWallet[]){
    let localBets:any[] = [];
    for(let counter=0;counter<bets.length;++counter){
      axios.get('https://chain.api.btc.com/v3/address/'+bets[counter].code)
      .then((response) => {
        let data = response.data.data
        let localBlock = bets[counter]
        localBlock.blockdata = data
        globalbets.push(localBlock)
        updateHtmlComponents(globalbets)
      })
      localBets.push(betRenderer(bets[counter]));
    }
    return localBets;
  }

  function betTextParser(bet:BitWallet){
    let intensityValue = 0;
    let betTypeText = ""
    let betCycleText = ""

    if(bet.intensity === INTENSITY_TYPE.low){
      intensityValue = 0.001
    }else if(bet.intensity === INTENSITY_TYPE.medium){
      intensityValue = 0.005
    }else{
      intensityValue = 0.05
    }

    if(bet.type === WALLET_TYPE.last){
      betTypeText = "Quem apostar por ultimo ganha"
    }else if(bet.type === WALLET_TYPE.most){
      betTypeText = "Quem transferir mais (BTC) ganha"
    }else{
      betTypeText = "outro"
    }
    
    if(bet.cycle ===CYCLE_TYPE.daily){
      betCycleText = "diaria"
    }else if(bet.cycle ===CYCLE_TYPE.weekly){
      betCycleText = "semanal"
    }else{
      betCycleText = "mensal"
    }
    return {intensityValue,betTypeText,betCycleText}
  }

  function betRenderer(bet:BitWallet){
    let textParseResult = betTextParser(bet);

    setTimeout(()=>{
      updateHtmlComponents(globalbets)
    }, 3000);

    return(
    <a className={"card"} data-bettype={bet.type}  data-betintensity={bet.intensity} data-betcycle={bet.cycle}>
      <div className="qrholdercontainer">
        {bet.qrcode
          ? <a href={"https://www.blockchain.com/btc/address/"+bet.code}  target="_blank"><div className={"qrholder"} dangerouslySetInnerHTML={{__html:bet.qrcode}} /></a>
          : null 
        }
      </div>
      <p><a href={"https://www.blockchain.com/btc/address/"+bet.code}  target="_blank">{bet.code}</a></p>
      <p>Valor minimo da aposta <b>{textParseResult.intensityValue} BTC</b></p>
      <p>Recompensa atual <b id={"balance"+bet.code}></b></p>
      <p>Total recebido  <b id={"received"+bet.code}></b></p>
      <p>Total transferido  <b id={"sent"+bet.code}></b></p>
      <p>Ciclo de aposta <b>{textParseResult.betCycleText}</b></p>
      <p><b>{textParseResult.betTypeText}</b></p>
    </a>
    )
  }

  return (
  <div>
    <div className="App container">
      {betsRenderer(BETS)}          
    </div>
    <ParticlesBg type="random" bg={true} />
  </div>
  );
}

export default App;
